import React, { useState } from "react"
import { Box, Text } from 'grommet';

const Home = ({ onClick, buttons, activeIndex }) => {

  const [active, setActive] = useState(activeIndex && buttons[activeIndex] || buttons && buttons[0])
  return <>
    <Box direction="row-responsive">
      {
        buttons && buttons.map((e, i) => <Box 
            key={i}
            onClick={() => (onClick && onClick(e, i), setActive(e))}
            align="center" color="white"  
            background={active.label === e.label ? 'brand' : 'black'} 
            fill="horizontal" 
            pad="small">
            <Text color="white"> { e.label }</Text>
          </Box>)
      }
    </Box>
  </>
}

export default Home
