import React, { useState, useEffect } from 'react';
import { TextInput, Box, Button  } from 'grommet';
import { parseISO, format, setMinutes, setHours, setYear, setMonth, setDate as setDateObjectDate } from 'date-fns';

const DateTimePicker = ({ label = 'Get Chart', dateObject, selectDate, onClick }) => {
    const [useDateObject, setDateObject] = useState(dateObject || new Date())
    
    const [year, setYearText] = useState(format(useDateObject, 'yyyy'));
    const [month, setMonthText] = useState(format(useDateObject, 'M'));
    const [day, setDayText] = useState(format(useDateObject, 'd'));
    const [date] = useState(useDateObject.toISOString());
    const [hour, setHour] = useState(format(useDateObject, 'H'))
    const [min, setMin] = useState(format(useDateObject, 'm'))

    useEffect(() => {
        let dateObj = parseISO(date);
        dateObj = setYear(dateObj, year)
        dateObj = setMonth(dateObj, month - 1)
        dateObj = setDateObjectDate(dateObj, day)
        dateObj = setHours(dateObj, hour)
        dateObj = setMinutes(dateObj, min)

        if(isNaN(dateObj.getTime())) {
            alert('invalid date')
        } else {
            setDateObject(dateObj)
        }
    }, [hour, min, date, year, month, day])

    return <>
            <Box
            direction="row"
            pad={{
                bottom: "small"
            }}
            >
                <TextInput focusIndicator value={year} onChange={event => setYearText(event.target.value) } />
                <TextInput focusIndicator value={month} onChange={event => setMonthText(event.target.value)} />
                <TextInput focusIndicator value={day} onChange={event => setDayText(event.target.value)} />
                <TextInput focusIndicator value={hour} onChange={event => setHour(event.target.value)} />
                <TextInput focusIndicator value={min} onChange={event => setMin(event.target.value)} />
            </Box>

        <Box align="end">
            
                <Button 
                onClick={() => {
                    onClick && onClick(useDateObject)
                    selectDate && selectDate(useDateObject)
                }}
                label={label}
                />
        </Box>
    </>;
}

export default DateTimePicker;