import React, { useState, useEffect } from "react"
import { Box } from 'grommet';
import styled from 'styled-components'
import DateTimePicker from '../components/DateTimePicker'
import Pillar from '../components/Pillar'
import BoxButton from '../components/BoxButton'
import { dateObjectToBazi } from '../cm-lib/FourPillar'
import { format, parseISO } from 'date-fns';
const queryString = require('query-string');

const isBrowser = typeof window !== `undefined`

const Home = ({ inputDate, onBazi }) => {

  const [bazi, setBazi] = useState(null)
  

  useEffect(() => {
    const qs = isBrowser && queryString.parse(window && window.location.search)
    const dt = isNaN(parseISO(qs.dt).getTime()) ? new Date() : parseISO(qs.dt)
    const baziChart = dateObjectToBazi(dt);
    setBazi(baziChart)
  }, [])
  return <>
      <BoxButton buttons={[
      { 
        label: 'M', 
        
      },
      {
        label: 'F',
      }
    ]} />
    <br />
    <DateTimePicker 
        onClick={date => {
          const dt = format(date, 'yyyy-MM-dd HH:mm')
          isBrowser && window.location.assign(`?dt=${dt}`)
        }}
    />
    <br />

    {
      bazi && bazi.GanZhiYear && <>
      <Pillar list={[bazi.GanZhiYear.ganzhi, "test2", "test2", "test2"]} />
      <Pillar list={["Test1", "test2", "test2", "test2"]} />
      <Pillar list={["Test1", "test2", "test2", "test2"]} />
      </>
    }
    {
      JSON.stringify(bazi)
    }

  </>
}

export default Home
